<template>
  <div id="add-to-collection-dialog-component">
    <el-dialog title="Add To Collection" v-model="visible">
      <div class="tag-container" v-if="tags.length">
        <FilterTag
          v-for="tag in tags"
          :key="tag.value"
          :data="tag.label"
          @close="onTagCloseClick(tag.value)"
        ></FilterTag>
      </div>
      <div class="select-item">
        <p>Collections:</p>
        <el-select v-model="selectValue" filterable placeholder="Select">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.checked"
          >
          </el-option>
        </el-select>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="success" @click="visible = false">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, watch } from "vue";
import { useStore } from "vuex";
import {
  DeleteCollectionFabric,
  GetFabricRelatedCollections,
  GetOwnCollections,
  PostCollectionCreateFabric,
  SetFabricRelatedCollections
} from "@/typings/store";
import { FabricRelatedCollectionReponse } from "@/typings/fabric";
import FilterTag from "@/components/FilterTag.vue";
import {
  CollectionAddFabric,
  CollectionUpdateFabricPriceParams
} from "@/typings/collection";
import { ElNotification } from "element-plus";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "AddToCollectionDialog",
  components: {
    FilterTag
  },
  setup() {
    const store = useStore();
    const fabricId = ref(0);
    const fabricPrice = ref(0);
    const visible = ref(false);
    const selectValue = ref("");

    const tags = computed(() => {
      return store.state.fabric.relatedCollections.map(
        (item: FabricRelatedCollectionReponse) => ({
          label: item.name,
          value: item.id
        })
      );
    });
    const options = computed<OptionItem>(() => {
      return store.state.collection.ownList.map(
        (item: FabricRelatedCollectionReponse) => ({
          label: item.name,
          value: item.id,
          checked: tags.value.some((it: OptionItem) => {
            return it.value === item.id;
          })
        })
      );
    });

    const init = async (id: number) => {
      await store.dispatch(GetFabricRelatedCollections, id);
      await store.dispatch(GetOwnCollections);
    };

    watch(selectValue, async id => {
      if (!id || typeof id !== "number") return;
      try {
        const params: CollectionUpdateFabricPriceParams = {
          collection_id: id,
          fabric_id: fabricId.value,
          price: fabricPrice.value
        };
        const data = await store.dispatch(PostCollectionCreateFabric, params);
        store.commit(SetFabricRelatedCollections, [
          ...store.state.fabric.relatedCollections,
          data
        ]);
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
      selectValue.value = "";
    });

    const open = async ({ id, price }: CollectionAddFabric) => {
      fabricId.value = id;
      fabricPrice.value = price;
      visible.value = true;
      await init(id);
    };

    const onTagCloseClick = async (id: number) => {
      try {
        const params: CollectionUpdateFabricPriceParams = {
          collection_id: id,
          fabric_id: fabricId.value
        };
        await store.dispatch(DeleteCollectionFabric, params);
        const formatData = store.state.fabric.relatedCollections.filter(
          (item: FabricRelatedCollectionReponse) => item.id !== id
        );
        store.commit(SetFabricRelatedCollections, formatData);
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
    };

    return {
      tags,
      options,
      visible,
      selectValue,

      open,
      onTagCloseClick
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#add-to-collection-dialog-component
  .select-item
    p
      margin 10px 0
      font-size 16px
      font-weight 600
    :deep() .el-select
      width 100%
</style>
